}

.post-list {
  margin: 0 0 40px;
  padding: 0;
}

.post-list li {
  border-bottom: 1px solid var(--global-divider-color);
  padding-top: 2rem;
  padding-bottom: 2rem;
  list-style: none;
}

.post-list li .post-meta {
