  margin-bottom: 0;
  font-size: .875rem;
}

.post-list li .post-tags {
  color: var(--global-text-color-light);
  padding-top: .25rem;
  padding-bottom: 0;
  font-size: .875rem;
}

.post-list li a {
  color: var(--global-text-color);
  text-decoration: none;
}

.post-list li a:hover {
