}

.projects .card img {
  width: 100%;
}

.projects .card-item {
  width: auto;
  margin-bottom: 10px;
}

.projects .card-item .row {
  align-items: center;
  display: flex;
}

.projects .grid-sizer, .projects .grid-item {
