  border-top: 1px solid var(--global-divider-color);
  text-align: right;
  margin-top: 2rem;
  margin-bottom: -2rem;
  padding-top: 1rem;
}

.publications ol.bibliography {
  margin-top: 0;
  padding: 0;
  list-style: none;
}

.publications ol.bibliography li {
  margin-bottom: 1rem;
}

.publications ol.bibliography li .preview {
  width: 100%;
  min-width: 80px;
