}

.publications ol.bibliography li .abbr {
  height: 2rem;
  margin-bottom: .5rem;
}

.publications ol.bibliography li .abbr abbr {
  background-color: var(--global-theme-color);
  padding-left: 1rem;
  padding-right: 1rem;
  display: inline-block;
}

.publications ol.bibliography li .abbr abbr a {
